.container {
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
  background: #262626;
}

.ul {
  margin: 0;
  padding: 0;
  display: -ms-flexbox;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.li {
  list-style: none;
  margin: 0 14px;
}
.li a {
  position: relative;
  display: block;
  width: 60px;
  height: 60px;
  text-align: center;
  line-height: 60px;
  background: #333;
  border-radius: 50%;
  font-size: 30px;
  color: #666;
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
}
.li a:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background: #ffee10;
  -webkit-transform: 0.5s;
      -ms-transform: 0.5s;
          transform: 0.5s;
  -webkit-transform: scale(0.9);
      -ms-transform: scale(0.9);
          transform: scale(0.9);
  z-index: -1;
}
.li a:hover:before {
  -webkit-transform: scale(1.1);
      -ms-transform: scale(1.1);
          transform: scale(1.1);
  -webkit-box-shadow: 0 0 15px #ffee10;
          box-shadow: 0 0 15px #ffee10;
}
.li a:hover {
  color: #ffee10;
  -webkit-box-shadow: 0 0 5px #ffee10;
          box-shadow: 0 0 5px #ffee10;
  text-shadow: 0 0 5px #ffee10;
}
.li a i {
  font-size: 30px;
}



